<template>
  <div class="publications-section">
    <h2>Publications Timeline</h2>
    <div class="legend">
      <span class="legend-item">
        <span class="legend-swatch conference"></span>
        Conference
      </span>
      <span class="legend-item">
        <span class="legend-swatch journal"></span>
        Journal
      </span>
    </div>
    <div class="timeline">
      <div class="year-group" v-for="group in visibleGroups" :key="group.year">
        <div class="year-label">{{ group.year }}</div>
        <div
          class="timeline-item"
          v-for="(pub, index) in group.items"
          :key="index"
          :style="{ '--i': index }"
        >
          <div class="timeline-item-connector"></div>
          <div class="timeline-content" :class="typeClass(pub.type)">
            <small class="pub-authors">{{ pub.authors }}</small>
            <div class="pub-title">{{ pub.title }}</div>
            <small class="pub-venue">
              <a :href="pub.link" target="_blank">{{ pub.venue }}</a>
            </small>
          </div>
        </div>
      </div>
    </div>
    <div v-if="hasMore" ref="loadMore" class="load-more">Loading more…</div>
  </div>
</template>

<script>
export default {
  name: "PublicationsTab",
  props: {
    groupedPublications: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      visibleCount: 10,
      pageSize: 10,
      observer: null
    };
  },
  computed: {
    flatItems() {
      const items = [];
      this.groupedPublications.forEach(group => {
        group.items.forEach(pub => {
          items.push({ year: group.year, pub });
        });
      });
      return items;
    },
    visibleGroups() {
      const groups = {};
      this.flatItems.slice(0, this.visibleCount).forEach(item => {
        if (!groups[item.year]) {
          groups[item.year] = [];
        }
        groups[item.year].push(item.pub);
      });
      return Object.keys(groups)
        .sort((a, b) => b - a)
        .map(year => ({ year, items: groups[year] }));
    },
    hasMore() {
      return this.visibleCount < this.flatItems.length;
    }
  },
  watch: {
    groupedPublications() {
      this.visibleCount = 10;
      this.$nextTick(() => {
        this.setupObserver();
      });
    }
  },
  methods: {
    typeClass(value) {
      if (!value) {
        return "";
      }
      const normalized = value.toString().trim().toLowerCase();
      if (normalized === "conference") {
        return "type-conference";
      }
      if (normalized === "journal") {
        return "type-journal";
      }
      return "";
    },
    setupObserver() {
      if (!("IntersectionObserver" in window)) {
        return;
      }
      if (this.observer) {
        this.observer.disconnect();
      }
      const target = this.$refs.loadMore;
      if (!target || !this.hasMore) {
        return;
      }
      this.observer = new IntersectionObserver(
        entries => {
          if (entries[0].isIntersecting) {
            this.visibleCount = Math.min(this.visibleCount + this.pageSize, this.flatItems.length);
          }
        },
        { rootMargin: "200px" }
      );
      this.observer.observe(target);
    }
  },
  mounted() {
    this.setupObserver();
  },
  updated() {
    this.setupObserver();
  },
  beforeUnmount() {
    if (this.observer) {
      this.observer.disconnect();
    }
  }
};
</script>

